import React, { Component, Fragment } from 'react';
import { connect } from "react-redux";
import queryString from 'query-string';
import { Tab, Nav } from 'react-bootstrap';
import moment from 'moment';
import PerfectScrollbar from 'react-perfect-scrollbar'
import { Link } from 'react-router-dom';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { notificationUpdateStatus, readAllNotification } from '../../../../Services/Actions/homeActions'
import { getLoadNotify } from '../../../../Services/Actions/loginActions'
import { SafariStatus } from '../../../../Services/Utils/utils'
class Notifications extends Component {
    constructor(props) {
        super(props)
        this.state = { 
            notifications: props.notificationList,
        }
    }
    updateStatus = (imNot) => {
        var self = this;
        if(!imNot.status){
            notificationUpdateStatus(imNot.id, 1).then((response) => {
                self.props.getLoadNotify()
            }).catch((ex)=>{{}})
        }
        this.props.onNotificationToggle()
        this.onRequest()
    }
    updateAllStatus = () => {
        readAllNotification().then(response => {
            this.props.getLoadNotify()
        })
    }
    getUnreadNotifications = (notifications) => {
        if(notifications && Object.keys(notifications).length>0){
            const unreadList = Object.entries(notifications).reduce((acc,[key, notify]) =>{
                const filtered = notify.filter(item=>item.status == 0)
                if(filtered.length > 0) acc.push([key,filtered])
                return acc;
            },[])
            return Object.fromEntries(unreadList)
        }
        else return {}
    }
    onRequest = () => {
        const parsed = queryString.parse(window.location.search);
        if ((parsed && Object.keys(parsed).length > 0) || String(window.location.href).includes("assets")) {
            const channel = SafariStatus ? window : new BroadcastChannel('connect');
            if (SafariStatus) window.opener.postMessage(JSON.stringify({ type: 'assets-redirect' })); else channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
        }
    }
    componentWillReceiveProps(nextProps){
        const {notificationList} = nextProps
        if(JSON.stringify(notificationList)!= JSON.stringify(this.state.notifications)) this.setState({notifications:notificationList})
    }

    render() {
        const { notifications } = this.state;
        const unreadNotifications = this.getUnreadNotifications(notifications)
        return <div className="w-100">
            <Tab.Container id="notifications_tab_panel" defaultActiveKey="all-notifications">
                <div className="d-flex align-items-center justify-content-between px-3 pt-2 border-bottom">
                    <Nav variant="underline" className="nav">
                        <Nav.Item className="">
                            <Nav.Link className="me-3 px-0 fw-medium" eventKey="all-notifications">All</Nav.Link>
                        </Nav.Item>
                        <Nav.Item className="">
                            <Nav.Link className="px-0 fw-medium" eventKey="unread-notifications">Unread</Nav.Link>
                        </Nav.Item>
                    </Nav>
                    <div>
                        <button type="button" className="btn btn-link btn-sm px-0 h-auto fw-bold text-decoration-none btn-active-color-primary" onClick={()=> Object.keys(unreadNotifications).length > 0 ? this.updateAllStatus() : {}}>Mark all as read</button>
                    </div>
                </div>
                <PerfectScrollbar options={{ wheelPropagation: false }}>
                    {notifications && Object.keys(notifications).length > 0 ? <Tab.Content>
                        <Tab.Pane eventKey="all-notifications" className="pb-3 min-h-100">
                            {
                                notifications && Object.keys(notifications).map((iNot, index) => {
                                    return <Fragment key={index}><div className="d-flex align-items-center justify-content-between px-3 py-1" >
                                        <span className="text-gray-800 fw-bold">{iNot}</span>
                                        <span className="bg-primary badge rounded-5 min-w-30 text-light-alt d-none">{(notifications[iNot] && notifications[iNot].length > 0) ? notifications[iNot].length : 0}</span>
                                    </div>
                                        {notifications[iNot] && notifications[iNot].length > 0 ? <div className="">

                                            {
                                                notifications[iNot].map((imNot) => {
                                                    return <Link to={imNot.url ? imNot.url : `#`} key={imNot.id} className={`px-3 d-flex align-items-start border-bottom notification-item text-decoration-none ${imNot.status == 0 ? `bg-light` : ``}`} onClick={()=>this.updateStatus(imNot)}>
                                                        <div className="symbol symbol-38 rounded-1 overflow-hidden me-2 bg-light p-2">
                                                            <img alt="Pic" src={baseUrl(`media/notification-icons/${imNot.message.photo}`)} height="40" width="40" onError={event=>event.target.src=baseUrl(`media/notification-icons/suite-icon.svg`)}/>
                                                        </div>
                                                        <div className="d-flex align-items-start flex-column flex-grow-1">
                                                            <div className="d-flex algin-items-center justify-content-between w-100">
                                                                <span className="text-hover-primary text-gray-800 fw-bold text-decoration-none me-2">{imNot.message.subject}</span>
                                                                <span className="text-gray-500 fs-9">{moment(new Date(imNot.message.time * 1000)).format("hh:mma")}</span>
                                                            </div>
                                                            <div className="d-flex algin-items-start">
                                                                <span className="text-gray-600 fs-9">
                                                                    {imNot.message.text}
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </Link>

                                                })
                                            }

                                        </div> : index == 0 ?
                                            <div className="block-center" style={{ height: 350 }}>
                                                <p className="mb-0 text-muted">No Notifications</p>
                                            </div>
                                            : null}
                                    </Fragment>

                                })
                            }
                        </Tab.Pane>
                        <Tab.Pane eventKey="unread-notifications" className="pb-3 min-h-100">
                            { unreadNotifications && Object.keys(unreadNotifications).length > 0 ? Object.keys(unreadNotifications).map((iNot, index) => {
                                return <Fragment key={index}>
                                    <div className="d-flex align-items-center justify-content-between px-3 py-1">
                                        <span className="text-gray-800 fw-bold">{iNot}</span>
                                        <span className="bg-primary badge rounded-5 min-w-30 text-light-alt d-none">{unreadNotifications[iNot] && unreadNotifications[iNot].length > 0 && unreadNotifications[iNot].filter((a) => a.status == 0).length > 0 ? unreadNotifications[iNot].filter((a) => a.status == 0).length : 0}</span>
                                    </div>
                                    {unreadNotifications[iNot] && unreadNotifications[iNot].length > 0 ?
                                        <div className="">
                                            {unreadNotifications[iNot].map((imNot) => {
                                                return imNot.status == 0 ? 
                                                <Link to={imNot.url ? imNot.url : `#`} key={imNot.id} className="px-3 d-flex align-items-start border-bottom notification-item bg-light text-decoration-none" onClick={()=>this.updateStatus(imNot)}>
                                                    <div className="symbol symbol-38 rounded-1 overflow-hidden me-2 bg-light p-2">
                                                            <img alt="Pic" src={baseUrl(`media/notification-icons/${imNot.message.photo}`)} height="40" width="40" />
                                                    </div>
                                                    <div className="d-flex align-items-start flex-column flex-grow-1">
                                                        <div className="d-flex algin-items-center justify-content-between w-100">
                                                            <span className="text-hover-primary text-gray-800 fw-bold text-decoration-none me-2">{imNot.message.subject}</span>
                                                            <span className="text-gray-500 fs-9">{moment(new Date(imNot.message.time * 1000)).format("hh:mma")}</span>
                                                        </div>
                                                        <div className="d-flex algin-items-start">
                                                            <span className="text-gray-600 fs-9">
                                                                    {imNot.message.text}
                                                            </span>
                                                        </div>
                                                    </div>
                                                </Link> : null

                                            })}
                                        </div>
                                    : null}
                                </Fragment> }) :
                                    <div className="block-center" style={{ height: 350 }}>
                                        <p className="mb-0 text-muted">No Unread Notifications</p>
                                    </div>    
                            }
                        </Tab.Pane>
                    </Tab.Content> : <div className="block-center" style={{ height: 150 }}>
                        <p className="mb-0 text-muted">No Notifications</p>
                    </div>}
                </PerfectScrollbar>
            </Tab.Container>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        notificationList: state.notifications.notificationList
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getLoadNotify: () => dispatch(getLoadNotify())
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Notifications);